import { Button } from "@carbon/react";
import { useEffect, useState } from "react";
import { connect } from "react-redux";

function Frame({ id, meta, metaState }: any) {
  const [url, setUrl] = useState("");
  useEffect(() => {
    setUrl(`https://sandbox.nftxledger.com/${id}`);
  }, [id]);

  const btnClick = () => {
    const elem = document.querySelector("iframe");
    if (elem) {
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.webkitRequestFullscreen) {
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) {
        elem.msRequestFullscreen();
      }
    }
  };

  return (
    <>
      <div style={{ border: "1px solid black" }}>
        <iframe src={url} width="500px" height="500px"></iframe>
      </div>
      <Button onClick={btnClick}>Fullscreen</Button>
    </>
  );
}

const mapStateToProps = (state: any) => ({
  id: state.item.id,
  meta: state.item.meta,
  metaState: state.item.metaState,
});

const mapDispatchToProps = (dispatch: any) => ({});

export default connect(mapStateToProps, mapDispatchToProps)(Frame);
